import { Router } from "@angular/router";
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { AfterViewInit, Component, ViewChild, OnInit , OnDestroy } from '@angular/core';
import { environment } from '../../../../environments/environment';
import { TranslateService } from '@ngx-translate/core';
import { UserService } from 'src/app/shared/Service/userService/user.service';
import { ToasterService } from 'src/app/shared/Module/toaster/toaster.service';
declare var $: any;
@Component({
  selector: 'app-top-navbar',
  templateUrl: './top-navbar.component.html',
  styleUrls: ['./top-navbar.component.css']
})
export class TopNavbarComponent implements OnInit {
  user;
  role;
  avatar;
  defaultImage="assets/img/error/unknown.jpg";
  VDMSURL=environment.VDMSURL;
  VDMSAVATARURL=this.VDMSURL+'assets/img/avatars/';
  formGroup: FormGroup;
  formData = new FormData();
  interval;
  currentLanguage="en";
  languageName="English";
  LanguageImage={
    en:"assets/img/country/us-flag.png",
    ch:"assets/img/country/china-flag.png",
  };
  constructor(
    private userService: UserService,
    private router : Router,
    private _formBuilder: FormBuilder,
    private toastr: ToasterService,
    private translate: TranslateService,
  ) { }

  ngOnInit(): void {
    this.user=this.userService.getToken()['userInfo'];
    this.role=this.userService.getToken()['role'];
    if(this.role=='agent'||this.role=='user') this.avatar=this.VDMSAVATARURL+this.user.avatar
    else this.avatar=this.VDMSAVATARURL+this.user.photo
    this.refresh();
    // this.checkLogin();
  }
  sidebar_toggle(){ 
    $("#ms-side-nav").toggleClass('ms-aside-open');
    $(".ms-aside-overlay.ms-overlay-left").toggleClass('d-block');
    $(".ms-body").toggleClass('ms-aside-left-open');
  }
  checkLogin(){
    const checkIfLogin = ()=>{
      if(this.userService.isLoggedIn()==false) this.router.navigateByUrl(this.userService.loginURL);
    }
   this.interval = setInterval(checkIfLogin,5000);
  }
  ngOnDestroy(){
    clearInterval(this.interval);
  }
  //LanguageChagePart
  changeLang(language: string) {
    this.currentLanguage=language;
    if(language=='en') this.languageName="English";
    else if(language=='ch') this.languageName="中文";
    localStorage.setItem('language',language)
    this.translate.use(language);
  }
  //@@@@@UserDropDown part
  //passwordReset
  cancel(){
    this.refresh();
  }
  savePassWord(){
        this.userService.postRequest('api/editPassword', this.formGroup.value).subscribe(
          res=>{
            this.Toaster('success','','success');
            this.refresh();
          },
          err=>{
            this.refresh();
            this.handleError(err)
          }
        );
  }
  refresh(){
    let phone='';
    if(this.role=='admin') phone=this.user.phone_number
    else if(this.role=='agent') phone=this.user.contact_number
    else if(this.role=='user') phone=this.user.phone
    const accountRegex = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z0-9]{11}$/;
    const intenationalPhone = /^1[3|4|5|7|8][0-9]{9}$/;
    const dateRegex=/^\d{4}-\d{2}-\d{2}$/;
    this.formGroup = this._formBuilder.group({
      phone:[phone],
      previous:['',Validators.required],
      password:['',Validators.required],
      confirm:[''],
      role:[this.role]
    });
  }
  passwordConfirm(){
    if(this.formGroup.value.password.length>0&&(this.formGroup.value.password!=this.formGroup.value.confirm))
    return false
    else if(this.formGroup.value.password.length>0&&(this.formGroup.value.password==this.formGroup.value.confirm)) return true
    else return true
  }
  //logOut
  logOut(){
    this.userService.logOut()
  }
  getName(){
    if('name' in this.user) return this.user.name;
    else return this.user.agent_name;
  }
  //toastr functions
  handleError(err){
    if(err.status==504)  this.Toaster('error', '', 'server is not responsing.',4000);
    else         this.Toaster('error', '', err.error.message,1500);
   }
  Toaster(style,msgtopic,msgContent,time=2000) {
    this.toastr.show(style, msgtopic, msgContent, time);
  }
}
